<template>
<div class="loginphone" v-wechat-title="$route.meta.title">
  <div class="login-box">
    <div class="box">
      <div class="title">登录</div>
      <div class="form">
        <div class="faster">
          <router-link to="loginmima">账号密码登录</router-link>
        </div>
        <el-input v-model="phone" placeholder="手机号"></el-input>
        <div class="sendcode">
          <el-input
            placeholder="短信验证码"
            class="code"
            v-model="code" >
            <!--v-on:keyup.enter="tologin"-->
          </el-input>
          <el-button type="primary" >获取验证码</el-button>
        </div>
      </div>
      <div class="forget">
        <div class="agree">登录即表示你同意 用户协议 和 隐私条款</div>
        <router-link  to="#">忘记密码</router-link>
      </div>
      <el-button type="primary" class="login">登录</el-button>
    <!--  <div class="other">
        <img class="weixin" src="@/images/weixin.png" alt="">
        <img class="weibo" src="@/images/weibo.png" alt="">
        <img class="qq" src="@/images/qq.png" alt="">
      </div>-->
    </div>
  </div>

</div>
</template>

<script>
    export default {
        name: "loginphone",
     data(){
          return{
            phone:'',
            code:''
          }
     }

    }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
.loginphone{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('~@/images/bg.png') center center no-repeat;
  .login-box{
    width:734px;
    height:463px;
    margin: 0 auto;
    background:rgba(255,255,255,1);
    box-shadow:0px 3px 12px 0px rgba(0, 0, 0, 0.1);
    border-radius:6px;
    display: flex;
    flex-direction: column;
    .box{
      width: 300px;
      margin: 0 auto;
      .title{
        margin: 36px 0;
        width: 100%;
        text-align: center;
        font-size:30px;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:34px;
      }
      .form{

        .faster{
          display: flex;
          justify-content: flex-end;
          a{
            color: $main;
            text-align: right;
            font-size:14px;
            font-weight:400;
            line-height:34px;
          }
        }
        .sendcode{
          margin-top: 12px;
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          .code{
            width: 150px;
          }
        }
      }
      .forget{
        width: 100%;
        margin-top: 10px;
        display: flex;
        justify-content: space-between;
        .agree{
          font-size:12px;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:34px;
        }
        a{
          font-size:12px;
          font-weight:400;
          color:$main;
          line-height:34px;
        }
      }
      .login{
        font-size: 16px;
        width: 100%;
        margin-top: 30px;
      }
      .other{
        margin-top: 38px;
        display: flex;
        justify-content: space-between;
        img{
          height: 39px;
        }
        .weixin,.weibo{
          width: 39px;
        }
        .qq{
          width: 30px;
        }
      }
    }



  }
}
</style>



